import 'bootstrap/dist/css/bootstrap.min.css';
import imageLogo from '../assets/logoIncasLab.png';
const Header = () => {
    return (
        <>
            <div className='col-12 justify-content-center d-grid bg-primary'>
                <div className='container'>
                    <img src={imageLogo} alt='logo' style={{ maxWidth: '200px', }}></img>
                </div>
            </div>
        </>
    );
}

export default Header;